import Vue from 'vue'
import App from './App.vue'
import router from './router'
import titleMixin from './title'
import bubblybg from 'bubbly-bg'

Vue.config.productionTip = false

Vue.mixin(titleMixin)

Vue.use(bubblybg)

bubbly({
  colorStart: "#0e263d",
  colorStop: "#151e27",
  blur: 1,
  bubbles: Math.floor((window.innerWidth + window.innerHeight) * 0.015),
  compose: "source-over",
  bubbleFunc: () => `hsla(${Math.random() * 300}, 50%, 50%, .3)`
})

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')